  //public commentDetails:any
  addLog(commentDetails){
    this.ticketService.addTicketComment(commentDetails).subscribe((res:any) => {
 
      if(res.message) {
        console.log('c2')
        this.isCommentSuccess = true;
        this.alertCommentMessage = "Comment added successfully";

        this.isTicketCommentLoaded = false;


        this.ticketService.getAllTicketCommentsByTicketId(this.ticketId).subscribe((res:any) => {

          this.isTicketCommentLoaded = true;
          this.ticketCommentsData = res.filter(item => {
            return item.isActive;
          });
          this.timeChanging(this.ticketCommentsData)   
          this.sortByDate(this.ticketCommentsData)                      
          _.each(this.ticketCommentsData, (item, index)=>{
            this.userService.getUserById(this.ticketCommentsData[index].insertedBy).subscribe((data:any) => {                  
              this.ticketCommentsData[index].userName = data[0].firstName + ' ' + data[0].lastName;
              this.ticketCommentsData[index].userId = data[0].userId;
            });
          });
        });
      }
      else {
        this.isCommentError = true;
        this.alertCommentMessage = res.message;
      }
    },
    error => {
      
    });
  }
