  /*model change functions starts */
  statusChange(event){    
    parseInt(event.target.value) === parseInt(this.ticket.ticketStatusId) ? this.statusChangeValue=true : this.statusChangeValue=false
   this.statusChangeValue==false ? this.changedStatus=event.target.options[event.target.selectedIndex].label :this.changedStatus=''
   
  }
  descChange(text :string){
    text === this.ticket.description ? this.statusChangeValue=true : this.statusChangeValue=false
  }
 


  public timeChanging =(data: any)=>{
    data.forEach(element => {   
      const time=moment.utc(element.insertedOn).fromNow()
      element.insertedOnClient= time
    });
  }

  public sortByDate = (data:any)=>{
    data.sort((a:any,b:any)=> new Date(b.insertedOn).getTime() -new Date(a.insertedOn).getTime())
  }

