import { Component, OnInit, Output, EventEmitter } from '@angular/core';

@Component({
  selector: 'app-print-dropdown',
  templateUrl: './print-dropdown.component.html',
  styleUrls: ['./print-dropdown.component.scss']
})
export class PrintDropdownComponent implements OnInit {

  printDataList:any;

  @Output() public outputParams = new EventEmitter();


  constructor() { }

  getPrintFormat(type){
    this.outputParams.emit(type);
  }

  ngOnInit(): void {

    this.printDataList = [{
      name : 'CSV',
      type: 'csv'
    }, {
      name : 'Excel',
      type: 'xls'
    },
    {
      name : 'PDF',
      type: 'pdf'
    },
    {
      name : 'HTML',
      type: 'html'
    }]

  }

}
