import { Component, OnInit } from '@angular/core';
import { SharedService } from '../../services/shared.service';

@Component({
  selector: 'app-custom-alert-messages',
  templateUrl: './custom-alert-messages.component.html',
  styleUrls: ['./custom-alert-messages.component.scss']
})
export class CustomAlertMessagesComponent implements OnInit {
  message:string = "";
  messagetype : string = "";
  isError:boolean = false;
  isSuccess:boolean = false;
  constructor(private sharedService:SharedService) { }


  closeMessage(){
    this.message = "";
    this.messagetype = "";
  }
  isMessageAvailable(){
  	return this.message != "" ? "show" : "";
  }

  ngOnInit() {
    this.sharedService.alerterrormessagecast.subscribe( data => {
      console.log('Mesg');
      console.log(data);
      this.message = data.msg;
      this.messagetype = data.type;
      if(data.type === 'Success'){
        this.isSuccess = true;
        this.isError = false;
      }
      if(data.type === 'Error'){
        console.log('err');
        this.isSuccess = false;
        this.isError = true;
      }

  		setTimeout(() => {
        this.message = "";
        this.messagetype = "";
  		}, 8000);

  	});
  }

}
